.block-catalog .block__content {
  display: flex;
  flex-flow: row wrap;
}

.block-catalog .block__items {
  display: grid;
  grid-template: auto/1fr 1fr;
  flex: 1 100%;
}

.block-catalog .block__links {
  display: flex;
  margin-top: 1px;
}

.block-catalog .block__links a {
  margin: auto;
}

.block-catalog-front .block__items .item:last-child:before {
  background: #fff;
}

.block-catalog-front .block__items .item:last-child .item__image {
  display: flex;
}

.block-catalog-front .block__items .item:last-child .item__image img {
  height: 33%;
  margin: auto;
  width: 33%;
}

.block-catalog.block-catalog-categories .block__items {
  display: grid;
  grid-template: auto/1fr;
  flex: 1 100%;
}

.block-catalog.block-catalog-category-with-goods .block__items {
  display: grid;
  grid-template: auto/1fr;
  flex: 1 100%;
  grid-gap: 1px;
}

@media only screen and (min-width: 450px) {
  .block-catalog .block__items {
    grid-template: auto/1fr 1fr 1fr;
  }
}

@media only screen and (min-width: 565px) {
  .block-catalog.block-catalog-categories .block__items {
    grid-template: auto/1fr 1fr;
  }
}

@media only screen and (min-width: 768px) {
  .block-catalog .block__items {
    grid-template: auto/1fr 1fr 1fr;
  }

  .block-catalog .block__links {
    margin: 40px 0 0;
  }

  .block-catalog.block-catalog-category-with-goods .block__items {
    grid-template: auto/1fr 1fr;
  }
}

@media only screen and (min-width: 1024px) {
  .block-catalog .block__links {
    margin: 80px 0 0;
  }

  .block-catalog.block-catalog-categories .block__items {
    grid-template: auto/1fr 1fr 1fr;
  }
}

@media only screen and (max-width: 1023px) {
  .block-catalog.block-catalog-categories .block__title {
    font-size: 24px;
  }
}

@media only screen and (max-width: 768px) {
  .block-catalog.block-catalog-categories {
    padding: 0;
  }
}

@media only screen and (max-width: 767px) {
  .block-catalog .block__links {
    background: #fff;
    padding: 40px 0 60px;
  }
}